@font-face {
  font-family: 'fontello';
  src: url('./fontello.eot?13988920');
  src: url('./fontello.eot?13988920#iefix') format('embedded-opentype'),
       url('./fontello.woff2?13988920') format('woff2'),
       url('./fontello.woff?13988920') format('woff'),
       url('./fontello.ttf?13988920') format('truetype'),
       url('./fontello.svg?13988920#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
