 [class^="bg-"]:before, [class*=" bg-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
 
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
 
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
 
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
 
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
 
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
 
  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}
 
.bg-plus:before { content: '\e800'; } /* '' */
.bg-restart:before { content: '\e801'; } /* '' */
.bg-warn:before { content: '\e802'; } /* '' */
.bg-light:before { content: '\e803'; } /* '' */
.bg-magic:before { content: '\f0d0'; } /* '' */
.bg-back:before { content: '\f104'; } /* '' */
.bg-send:before { content: '\f1d9'; } /* '' */
.bg-share:before { content: '\f1e0'; } /* '' */


